body {
  margin: 0;
  padding: 0;
  background-color: rgb(2, 45, 49);
  font-family: "Dancing Script", cursive;
  color: bisque;
  padding-bottom: 200px;
}

.section {
  display: grid;
  height: 100vh;
  display: flex; 
  justify-content: center; 
  align-items: center;
  grid-template-columns: repeat(2, 1fr); 
  gap: 300px;

  &__text{
    font-family: "Dancing Script", cursive;
    text-align: right;
    color: bisque;

    &--main {
      font-size: 60px;

    }

    &--info {
      font-size: 30px;
    }
  }

  &__photo img {
    height: 400px;
    width: auto;

    /* 🔽 ЭФФЕКТ РАЗМЫТЫХ КРАЕВ */
    -webkit-mask-image: radial-gradient(circle at center, black 70%, transparent 100%);
    mask-image: radial-gradient(circle at center, black 70%, transparent 100%);
    -webkit-mask-repeat: no-repeat;
    mask-repeat: no-repeat;
    -webkit-mask-size: 200% 200%;
    mask-size: 100% 100%;
    mask-position: center;
    -webkit-mask-position: center;
  }
}

.education {
  display: grid;
  grid-template-columns: repeat(2, 1fr); 
  gap: 100px;

  color: bisque;
  padding: 80px 20px;
  font-family: "Manrope", sans-serif;


  &-wrapper {
    padding-inline: 60px;
  }

  .education__block {
  position: relative;
  padding: 40px 30px;
  margin: 50px auto;
  border-radius: 16px;
  background: rgba(255, 255, 255, 0.03);
  z-index: 1;
  overflow: hidden;

  // 💜 ЭФФЕКТ СВЕЧЕНИЯ
  &::before {
    content: "";
    position: absolute;
    top: -20%;
    left: -20%;
    width: 140%;
    height: 140%;
    background: radial-gradient(circle at center, rgba(80, 180, 170, 0.25), transparent 70%);
    filter: blur(70px);
    z-index: -1;
    pointer-events: none;
  }

  box-shadow: 0 0 30px rgba(255, 255, 255, 0.06);
}


  &__title {
    text-align: center;
    font-size: 26px;
    font-weight: bold;
    margin-bottom: 30px;
    text-transform: uppercase;
    letter-spacing: 1px;
  }

  &__item {
    margin-bottom: 30px;
  }

  &__date {
    font-weight: bold;
    color: #ffc;
    display: block;
    margin-bottom: 6px;
  }

  &__degree {
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 6px;

    a {
      color: bisque;
      text-decoration: underline;

      &:hover {
        color: white;
      }
    }
  }

  &__location {
    font-size: 14px;
    font-style: italic;
    margin-bottom: 8px;
  }

  &__desc {
    font-size: 14px;
    line-height: 1.6;
  }

  &__image {
    text-align: center;
    margin-top: 30px;

    img {
      max-width: 100%;
      border-radius: 12px;
      box-shadow: 0 0 30px rgba(255, 255, 255, 0.1);
    }
  }
}

.text {
  display: flex; 
  justify-content: center; 
  align-items: center;
  font-size: 40px;
}


.education__wrapper2 {
    display: grid;
    grid-template-columns: repeat(2, 1fr); 
    gap: 200px;
    padding-inline: 60px;
}
.education__block--image {

  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0; // убрать текстовый отступ

  img.education__img {
    width: 100%;
    aspect-ratio: 1 / 1; // квадрат
    object-fit: cover;
    border-radius: 12px;
    box-shadow: 0 0 30px rgba(255, 255, 255, 0.1);
  }
}

@media (max-width: 768px) {
  .section {
    flex-direction: column;
    gap: 40px;

    &__text {
      text-align: center;

      &--main {
        font-size: 40px;
      }

      &--info {
        font-size: 20px;
      }
    }

    &__photo img {
      height: 280px;
    }
  }

  .education-wrapper {
    padding-inline: 20px;
  }

  .education {
    grid-template-columns: 1fr;
    gap: 60px;

    .education__block {
      margin: 30px auto;
      padding: 30px 20px;
    }

    .education__title {
      font-size: 20px;
    }

    .education__desc,
    .education__location,
    .education__degree,
    .education__date {
      font-size: 13px;
    }
  }

  .education__wrapper2 {
    grid-template-columns: 1fr;
    gap: 40px;
    padding-inline: 20px;
  }

  .education__block--image img.education__img {
    max-width: 100%;
    height: auto;
    aspect-ratio: 1 / 1;
  }

  .text {
    font-size: 28px;
    text-align: center;
  }
}

.bg-hearts {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  pointer-events: none;

  .heart {
    position: absolute;
    font-size: 24px;
    color: rgba(0, 255, 0, 0.3);
    user-select: none;
  }
}

.peek-cat-wrapper {
  position: fixed;
  top: 30%;
  left: -120px; // прячем за экран
  width: 120px;
  height: 120px;
  z-index: 999;
  pointer-events: none;

  canvas {
    width: 100%;
    height: 100%;
  }
}

.footer-cat {
  position: fixed;
  bottom: 0;
  right: 0;
  width: 140px;
  height: 140px;
  pointer-events: none;
  z-index: 999;
}

.thank-you {
  text-align: center;
  font-size: 40px;
  color: lightgreen;
  opacity: 0;
  transform: scale(0.9);
}

